.main {
  display: block;
  flex-grow: 1;
}

.row {
  display: flex;
  align-items: flex-start;
}

.warning {
  flex: 0 0 200px;
  margin-left: 25px;
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 8px;

  @include notebook {
    display: none;
  }
}

.apps {
  flex-grow: 1;
}
</style>
